body {
  background-color: #aadfa5;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

input[type="text"] {
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 25%;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 16px;
}

.country-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.country-list li {
  border-bottom: 1px solid #eee;
  align-items: center;
  padding: 10px;
  display: flex;
}

.country-list li img {
  margin-right: 10px;
}

.country-info {
  margin-top: 20px;
}

.country-info img {
  border-radius: 5px;
  margin: 0 auto 10px;
  display: block;
}

.country-info h1 {
  text-align: center;
  margin-bottom: 10px;
  font-size: 24px;
}

.country-info p {
  text-align: center;
  margin-bottom: 5px;
}
